import axios from "axios";

//获取该时间段内的执行情况数据
const getQuesnaireEditApi = (todoListId, tickSet) => new Promise((resolve, reject) =>{
    let data = {
        todoListId: todoListId,
        tickSet: tickSet,
      };
    axios({
        method:'post',
        url:'/api/todolist/tick',
        headers:{
            "Content-Type": "application/json",
        },
        data: JSON.stringify(data),
    }).then((response)=>{
        resolve(response)
    }).catch((error)=>{
        reject(error.response)
    })
})

export{
    getQuesnaireEditApi
}