<!-- 普通用户--填写评分问卷页面 -->
<template>
    <div class="main">
        <div class="quesTitle">
            <span class="title">成长评估表</span>
            <span class="headNote">
                <span>公司文化九条，要求思想上开放进取(O)，作风上把打胜仗作为信仰(W)，业务上创新求实(I)，工作中信任互助(H)，员工间尊重平等(R)，行为上业精于勤(D)，执行力使命必达(M)，团队上五湖四海(F)，成长上坚持自省(I)。本需求旨在设计一个成长评估系统，将上述文化九条作为评估依据，实现公司内部人员的交叉互评。在填写下面评估表时，</span>
                <span style="font-weight: bold;">请您遵守以下原则：</span>
                <span>确保您的评价客观、公正，基于具体的工作表现和事例；避免使用模糊不清、可能引起误解或带有个人偏见的语言，确保沟通清晰、直接；鼓励给予正面的反馈，同时诚恳地指出改进的空间，帮助同事成长；尊重每位同事的独特个性与贡献，认识到每个人都是团队成功不可或缺的一部分。</span>
            </span>
            <span class="evaluation">您当前的评估对象为：</span><span class="evaluationName">{{ evaluatedName }} / {{
                department }}部</span>
        </div>
        <div class="quesContent">
            <a-form :model="formState" name="basic" autocomplete="off">

                <a-form-item v-for="(question, index) in questions" :key="question" :label="'维度' + (index + 1)"
                    :name="'question' + index" :rules="[{ required: true, message: '' }]">
                    <div class="questionTitle">{{ question }}</div>
                    <div class="questionOptions">
                        <a-radio-group v-model:value="formState[index]">
                            <a-radio v-for="(option, optionIndex) in options" :value="optionIndex" :key="option"
                                style="font-size: 16px; line-height: 40px; width: 200px;">
                                {{ option }}
                            </a-radio>
                        </a-radio-group>
                    </div>
                </a-form-item>

                <a-form-item class="submitBtn">
                    <a-button type="primary" @click="handleSubmit" style="margin-right: 30px;">确认</a-button>
                    <a-button type="primary" ghost @click="jumpToList">取消</a-button>
                </a-form-item>
            </a-form>
        </div>
    </div>
</template>

<script setup>
import { onMounted, ref } from "vue"
import { message } from 'ant-design-vue'
import { useRouter } from 'vue-router';
import { getQuesnaireEditApi } from '@/api/quesnaireEdit'

onMounted(() => {
})

// 从session中获取被评估人信息
const evaluatedName = ref(sessionStorage.getItem('evaluatedName'))
const department = ref(sessionStorage.getItem('department'))
const todoListId = sessionStorage.getItem('todoListId')

const router = useRouter();
const formState = ref(['', '', '', '', '', '', '', '', ''])
const questions = ref(['思想上：开放进取（O）', '作风上：把打胜仗作为信仰（W）', '业务上：创新求实（I）', '工作中：信任互助（H）', '员工间：尊重平等（R）', '行为上：业精于勤（D）', '执行力：使命必达（M）', '团队上：五湖四海（F）', '成长上：坚持自省（I）'])
const options = ref(['成为榜样（5分）', '成为习惯（4.5分）', '经常正感知（4分）', '偶尔正感知（3.5分）', '无感知（3分）', '偶尔负感知（2.5分）', '经常负感知（2分）'])
// 映射规则
const scoreMap = {
    0: 5,
    1: 4.5,
    2: 4,
    3: 3.5,
    4: 3,
    5: 2.5,
    6: 2
};
// 调用api-提交用户填写的问卷数据
const handleSubmit = () => {
    // 如果formState列表中不存在空字符串，则提交数据
    if (formState.value.indexOf('') === -1) {
        // 使用 Array.map() 来映射每个值
        const tickSet = Array.from(formState.value).map(value => scoreMap[value] || value); 
        getQuesnaireEditApi(todoListId, tickSet).then(res => {
            if (res.data.code === 200) {
                message.success("提交成功！^_^")
                if(localStorage.getItem('userRole')==='0'){
                    router.push({ path: '/todoList' })
                }else{
                    router.push({ path: '/todoListAd' })
                }
            } else {
                message.error("提交失败！" + res.data.msg)
            }
        }).catch((err) => {
            console.log(err)
        })
    } else {
        message.error('请再次检查评估表是否填写完整！');
    }
}
const jumpToList = () => {
    if(localStorage.getItem('userRole')==='0'){
        router.push({ path: '/todoList' })
    }else{
        router.push({ path: '/todoListAd' })
    }
}
</script>

